@import "../../../style-config/variable.scss";

.section{
    padding: 100px 0;
    position: relative;
    display: flex;
    justify-content: center;
    &.light{
        background-color: $black-light;
    }
    &.dark{
        background-color: $black;
    }
    .content{
        width: 1200px;
        padding: 0 20px;
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .section-title{
        width: 100%;
        text-align: center;
        color: $white;
        max-width: 500px;
    }
}