@import "../../../style-config/mixin.scss";
@import "../../../style-config/variable.scss";

.class-info-box{
    background-color: $black-lighter;
    width: 355px;
    padding: 30px;
    border-radius: 6px;
    color: $gray;
    @include small-sreen{
        width: 100%;
        margin-top: 20px;
    }
    h4{
        line-height: 34px;
        margin-bottom: 20px;
    }
    .contact-option{
        display: flex;
        align-items: center;
        margin-top: 15px;
        svg{
            color: $yellow;
            font-size: 16px;
        }
        .text{
            color: $white;
            margin-left: 15px;
        }
    }
}