@import "../../../style-config/mixin.scss";
@import "../../../style-config/variable.scss";

.blog-card{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 528px;
    width: calc(100% / 3);
    border-radius: 16px;
    overflow: hidden;
    margin-right: 20px;
    cursor: pointer;
    @include small-sreen{
        margin-bottom: 20px;
        width: 100%;
    }
    .image-section{
        width: 100%;
        height: 250px;
        overflow: hidden;
        @include transition-ease;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: center;
            display: block;
        }
    }
    .content-section{
        display: flex;
        flex-direction: column;
        padding: 30px;
        padding-bottom: 55px;
        position: relative;
        flex-grow: 1;
        background-color: $white;
        @include transition-ease;
        .info-bar{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20px;
            .user-name , .posted-date{
                @include typography(18px , 500 , 20px);
            }
        }
        h3{
            margin-bottom: 10px;
            @include ellipsis;
        }
        p{
            color: $black-light;
            @include ellipsis;
        }
        .read-more-cta{
            display: flex;
            align-items: center;
            position: absolute;
            bottom: 30px;
            left: 30px;
        }
        .text{
            @include typography(18px , 500 , 20px);
        }
      }  
&:hover{
    .image-section{
        transform: scale(1.2);
    }
    .content-section{
        background-color: $black-light;
        color: $white;
        p{
            color: $white;
        }
    }
}
}